@import "../../styles/imports";

.catalog {
  max-width: 1136px;
  padding-top: 20px;
  
  @include on-small-pc {
    padding-inline: 20px;
  }

  &__head {
    margin-bottom: 40px;
  }

  &__wrapper {
    width: 100%;
  }

  &__title {
    font-size: 32px;
    font-weight: 700;
    color: #313237;
    margin-bottom: 8px;
  }

  &__models-quantity {
    font-size: 14px;
    font-weight: 500;
    color: #89939A;
  }

  &__select {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }

  &__organazing {
    display: flex;
    gap: 16px;
    margin-bottom: 24px;
  }

  &__label {
    font-size: 12px;
    font-weight: 600;
    color: #89939A;
  }

  
}
