@import "../../styles/imports";

.cart-card {
  width: 100%;
  max-width: 750px;
  border: 1px solid #E2E6E9;
  padding: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  margin-bottom: 16px;

  &__delete {
    width: 32px;
    height: 32px;
    border: 1px solid #B4BDC3;
    background-color: #fff;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url(../../assets/images/icons/card-icons/close.svg);
  }

  &__image {
    height: 66px;
  }

  &__title {
    font-family: Mont, "Helvetica Neue", Helvetica, sans-serif;
    width: 100%;
    font-size: 14px;
    font-weight: 500;
    text-align-last: left;
    color: #313237;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

  }

  &__prices {
    width: 100%;
    text-align-last: left;
    margin-top: 7px;
    display: flex;
    gap: 8px;

    &-price {
      font-size: 22px;

      &--current {
        font-weight: 700;
        color: #313237;
      }

    }
  }

  &__actions {
    display: flex;

    &__quantity {
      display: flex;
      align-items: center;
      width: 32px;
      height: 32px;
      line-height: 32px;
      text-align: center;
      font-size: 14px;
      font-weight: 500;
    }

    &__button {
      width: 32px;
      height: 32px;
      border: 1px solid #B4BDC3;
      background-color: #fff;
      background-position: center;
      background-repeat: no-repeat;

      &--add {
        background-image: url(../../assets/images/icons/card-icons/plus.svg);
      }

      &--delete {
        background-image: url(../../assets/images/icons/card-icons/minus.svg);
      }

      &--delete-all {
        background-image: url(../../assets/images/icons/card-icons/close.svg);
      }
    }
  }

}
