@import "../../styles/imports";

.search {
  &__input {
    height: 64px;
    width: 300px;
    font-size: 14px;
    font-family: inherit;
    font-weight: 600;
    padding-inline: 24px;
    border: none;
    outline: none;
    border-bottom: 1px solid #E2E6E9;
    border-left: 1px solid #E2E6E9;
    background-color: #fff;
    background-image: url("../../assets/images/icons/header-icons/search.svg");
    background-repeat: no-repeat;
    background-position: center right 24px;

    @include on-tablet-small {
      width: 100%;
      height: 80px;
      background-size: 30px;
      font-size: 20px;
      border: 2px solid #E2E6E9;
    }

    &::placeholder {
      color: #B4BDC3;
    }
  }
}
